import React from 'react'

function Projects({image,projectName,link,link2}) {
    return (
        <div className="project">
            <img className="projectimg rotate " src={image} alt=""/>
            <div className="desc">
                <h1 className="pn">{projectName}</h1>
                <div className="link">
                <div className="links">
                    <a className="link1" href={link} target="_blank"> View Live </a>
                    <a className="link1" href={link2} target="_blank"> Source Code </a>
                </div>
                </div>         
            </div>
        </div>
    )
}

export default Projects
